export const title = 'Раньше Казахстан финансировал из госбюджета 173 вида спорта. Теперь лавочка закроется?';
export const Body = () => (<>
<p>Коррупция, падающий спортивный имидж страны, легионеры, увозящие из страны миллиарды, отрезвили, кажется, всех. Финансирование спорта в Казахстане планируют полностью изменить. Что там с деньгами, выясняла журналистка Orda.kz.</p>
<p><b>Хотим как у всех</b></p>
<p>Тема бюджетов, которые выделялись на спорт, эффектно подсветилась по итогам Олимпийских игр — 2024. Цифры стали мусолить. Выяснилось, что в Казахстане на государственные деньги существует 173 вида спорта.</p>
<p>Кажется, что сама цифра бюджетных секций передаётся между ведомствами по глухому телефону. То говорится, что дотационных видов 182, то 180, то 172, то 178, однако нам в ответе на запрос дали цифру 173, от неё и пляшем.</p>
<p>Ответ Минтуризма Odra.kz</p>
<p>При этом признанных олимпийских дисциплин только 40.</p>
<p>Если не заострять внимание на том, что фантазия рядового гражданина иссякнет после перечисления 30–40, откуда в Казахстане 173 бюджетные секции, трудно объяснить.</p>
<p>Пошли разговоры, что во всех развитых странах, конечно, ничего подобного нет. Первым об этом  на брифинге сказал бывший глава Минтуризма Ермек Маржикпаев. В декабре 2023 года он презентовал законопроект. Именно в его отчёте появились цифры, где говорилось, что в Казахстане в 12 раз больше бюджетных спортивных видов, чем, например, в Великобритании.</p>
<p>Выше мы уже приводили цифру, в которую обходятся 173 секции, — почти 75 млрд тенге. В то же время в России за 2023 год, где финансируется в шесть раз меньше спортивных дисциплин, потратили 844 млрд рублей  или 4 трлн 61 млрд тенге; но, конечно, в РФ в семь раз больше численность населения, чем в Казахстане. В Германии, где тоже есть ограниченное количество видов спорта, которые финансируются из бюджета, в 2024 потратили 260 млн евро или 146,5 млрд тенге — а в Германии живёт вчетверо больше людей, чем у нас, но спортивный бюджет лишь вдвое больше.</p>
<p><b>Ещё немного примерных подсчётов</b></p>
<p>Грубо произведём расчёты в рамках подушевого финансирования. Для примера возьмём Казахстан и Россию.</p>
<p>Потенциально на одного человека в РФ тратится в семь раз больше денег, чем в Казахстане, вопреки малочисленным спортивным видам на государственном содержании. В России госбюджетом поддерживается 31 дисциплина.</p>
<p>Вне зависимости от того, по скольким «корзинам» будут распределяться «яйца», ориентироваться стоит всё-таки на качество этих инвестиций, то есть объём того самого денежного довольствия. Bозможно, от этого зависит результат?</p>
<p>На Олимпийских играх в 2016 в Рио-де-Жанейро в медальном зачёте Россия взяла четвёртое место, Германия пятое, а Казахстан — 22.</p>
<p>2020-й с Олимпийскими играми в Токио позволил Казахстану занять лишь 83 строчку медального зачёта, Германии девятую, России пятую, а Республике Корея — 16-ю.</p>
<p>На Олимпийских играх 2024 года в Париже мы не можем говорить о медальном зачёте России, так как страна выступала под нейтральным флагом, Германия взяла 10 место, а Казахстан — 43. Тут же внезапно восьмую строчку медального зачёта захватила Южная Корея. А что у них с бюджетом?</p>
<p>Годовые инвестиции государства в спорт в Южной Кореи составляют 1 трлн 670 млрд вон или 597,5 млрд тенге.</p>
<p>Это меньше в 2,4 раза по сравнению с Россией и больше в 3,12 раза по сравнению с Казахстаном, а также почти в семь раз по сравнению с Германией.</p>
<p>Ещё раз, это грубый подсчёт того, как финансируется спорт в странах, бюджеты которых мы нашли в открытом доступе.</p>
<p>С грубой математикой можно покончить. К сожалению, найти точную цифру количества видов спорта, которые финансируются из бюджета, в государствах, перечисленных Ермеком Маржикпаевым, мы не смогли.</p>
<p>И хочется отметить США: там государственные деньги на спорт почти вообще не выделяются, но об этом не говорил наш бывший министр.</p>
<p>Какой вывод мы можем сделать? Государства-олимпийские лидеры совсем иначе ведут себя с финансированием. Опыт каждой страны уникален. Ориентироваться только на то, что в более успешных странах из бюджета дотируют ограниченное количество дисциплин, кажется, недостаточно. Не были упомянуты бюджеты этих стран, их население и состояние спортивной инфраструктуры, а это тоже очень важно.</p>
<p><b>Как поступим?</b></p>
<p>Принцип распределения прост: кто выигрывает медали, тот и получает больше денег.</p>
<p>Когда экс-министр Ермек Маржикпаев презентовал законопроект с перечислением стран, которые финансируют так мало спортивных дисциплин (не приводя при этом бюджетов), Ербол Мырзабосынов в качестве председателя комитета по делам спорта и физической культуры МТС РК был рядом.</p>
<p>И вот спустя почти год Ербол Мырзабосынов уже в роли министра туризма и спорта Республики Казахстан законопроект презентовал общественности.</p>
<p>ÐÐ»Ð»ÑÑÑÑÐ°ÑÐ¸Ñ: ÐÐ¸Ð½Ð¸ÑÑÐµÑÑÑÐ²Ð¾ ÑÑÑÐ¸Ð·Ð¼Ð° Ð¸ ÑÐ¿Ð¾ÑÑÐ° Ð Ð</p>
<p>Иллюстрация: Министерство туризма и спорта РК (13.11.2024)</p>
<p>Теперь будут внедрять новую систему отбора, которая ориентирована строго на олимпийские виды спорта.</p>
<p>«Согласно законопроекту, приоритетные виды спорта будут отбираться не по мировым рейтингам, а на основе результатов выступлений национальных сборных на Олимпийских, Паралимпийских, Сурдлимпийских, Азиатских и Параазиатских играх».</p>
<p>Один из пунктов законопроекта предусматривает «предотвращение избыточного законодательного регулирования». Здесь опять упоминается цифра 180, хотя месяцем ранее нам в ведомстве сказали только о 173 (дисциплинах, финансирующихся из бюджета).</p>
<p>«В Казахстане в настоящее время развиваются более 180 видов спорта. Однако каждый год медали завоёвываются лишь в 13 летних и пяти зимних олимпийских видах спорта. Если в 2018 году казахстанские спортсмены завоевали 370 медалей, то в 2020 году этот показатель сократился до 105. Такая ситуация объясняется недостатками в системе подготовки спортсменов на всех этапах, начиная с начальной подготовки и заканчивая спортом высших достижений».</p>
<p>В законопроекте упоминается, что детско-юношеские спортивные школы и спортивные интернаты — организации для бюджета весьма дорогие. За два года от сердца оторвали 646 млрд государственных тенге, и оказалось, что они выброшены в пустоту, ведь:</p>
<p>«Почти 72 % были потрачены на виды спорта, не являющиеся приоритетными для страны».</p>
<p>В числе прочего как одну из проблем нынешней ситуации в спорте планируют устранить разобщённость федераций. Из-за того что якобы каждая федерация сама себе хозяйка, размывается ответственность и замедляется развитие спорта. Вот что решили в ведомстве:</p>
<p>«Предлагаемая новая модель предусматривает подчинение местных федераций аккредитованным республиканским (или региональным) спортивным федерациям. Это изменение направлено на централизацию управления в сфере развития спорта, повышение эффективности и чёткое определение ответственности».</p>
<p>Ещё один особенно интересный момент — это формулировка закона, предусматривающая единый стандарт подготовки.</p>
<p>«Согласно законопроекту, спортивный резерв будет готовиться исключительно по видам спорта, относящимся к спорту высших достижений. Подготовка спортсменов будет проводиться строго в соответствии с национальными стандартами спортивной подготовки, установленными уполномоченным органом».</p>
<p>Кроме этого, рамки финансирования появятся у профессиональных клубов, а легионеров, которые не являются гражданами Казахстана, запретят финансировать из государственного и квазигосударственного сектора.</p>
<p>Вводится понятие «сопровождающий спортсмен с инвалидностью».</p>
<p>«Принятие документа значительно повысит инклюзивность в сфере спорта и создаст большие возможности для поддержки побед спортсменов с инвалидностью», — гласит законопроект.</p>
<p>О чём все эти нововведения и как они могут отразиться, разбираемся с Алексеем Кондратом, заслуженным тренером РК, почётным вице-президентом Азиатской ассоциации.</p>
<p>Обсуждение мы начали с бюджета. Несмотря на то, что в официальном ответе указана цифра почти в 75 млрд тенге, денег, которые находятся в спорте, по словам эксперта, гораздо больше.</p>
<p>«Вряд ли здесь полностью учитываются закрытые от общественности бюджеты профессиональных игровых, и не только игровых, клубов. О том, сколько выделяют футбольным, хоккейным клубам частные компании, не говорится нигде. Очевидно, 75 млрд тенге — это те деньги, которые выделяются Минтурспорта. Сюда не входят деньги, которые выделяются в регионах, а также деньги Фонда развития инноваций, туризма и спорта SportQory»,</p>
<p>объясняет Алексей Кондрат.</p>
<p>Алексей подмечает: говорить о том, где и тем более насколько эффективно эти деньги используются, не представляется возможным, ведь цифры всегда общие.</p>
<p>Однако ухудшение позиций Казахстана в мировом спорте и отчёты Антикора, где говорится, что спорт — одна из сфер, где воруют, не стесняясь, благоприятной обстановке отнюдь не способствуют.</p>
<p>Читайте также: Миллиардные хищения, тренеры без дипломов и судья-взяточник: в Антикоре рассказали о самых скандальных делах</p>
<p>«В регионах существует практика пересмотра бюджета на спорт в сторону увеличения. Свежий пример: рассмотрение в Шымкенте тендера на финансирование развития футбола. Сумма 980 млн тенге должна быть освоена до конца 2024 года, то есть за два месяца. Сезон окончен, поездок нет. Как думаете, освоят?»</p>
<p>задаётся риторическим вопросом эксперт.</p>
<p><b>Пир во время чумы</b></p>
<p>Один из показательных примеров «освоения» бюджета может выступить лёгкая атлетика. Мы писали о том, что именно в этом виде спорта есть масса проблем. Условия тренировок в регионах часто невыносимые, и совсем скоро и в крупных городах инфраструктура потеряет эффективность, но номинально будет «развиваться».</p>
<p>И в это же время в Астане проходят соревнования мирового уровня.</p>
<p>«В январе 2024 года были организованы соревнования World Athletics Indoor Continental Tour Golden Level. В Астану приехали спортсмены из 37 стран, включая Багамы, Ямайку, Эфиопию, Нигерию, Люксембург. А кроме них — США, Великобритания, Германия, Кения и другие. Были приглашены звёзды первой величины. Мировой рекордсмен в прыжках с шестом Арманд Дюплантис, одна из мировых лидеров в барьерном беге Тоби Амусан и другие. Всего в соревнованиях участвовали 134 атлета, из них 26 — из Казахстана. Большая часть наших спортсменов просто составляла фон, могли бы и не выступать. Только в одном виде программы, в прыжках в высоту, наша спортсменка попала в призёры, и ещё двое атлетов заняли четвёртые места. Все остальные занимали, как правило, последние места в забегах или последние места в прыжках и метаниях»,</p>
<p>с горечью говорит Алексей Кондрат.</p>
<p>По словам тренера, делалось это для имиджа, чтобы пришли дети, посмотрели на мировых звёзд и захотели так же и даже лучше. Только результат был обратный, разочарованные зрители, видя неуспех соотечественников, уходили прямо во время соревнований.</p>
<p>«На сайте Федерации писали, что призовой фонд составит 100 тысяч долларов. Кроме этого, зарубежным атлетам оплачивали перелёт, питание и размещение в пятизвёздочном отеле, а многим давали и деньги за участие. Как правило, оплачиваются и услуги международных чиновников из World Athletics, помогающих в организации подобного рода соревнований. Прибавьте к этому транспорт, ТВ-трансляцию, рекламу и прочее. Словом, "праздник" для зарубежных атлетов удался на славу, но наши на этом празднике явно были на вторых ролях. Какова сумма, потраченная на это однодневное мероприятие, неизвестно. Какие суммы подъёмных были выплачены звёздам мировой лёгкой атлетики, тоже неизвестно. Но, по скромным подсчётам, эти однодневные соревнования обошлись не менее чем в 500 тысяч долларов. Возможно, больше. Скорее всего, деньги были выделены через Государственный фонд развития туризма и спорта SportQory», — предполагает эксперт.</p>
<p>Редакция Orda.kz направила запрос в SportQory, чтобы выяснить точные затраты на соревнование.</p>
<p><b>У слабых нет шансов</b></p>
<p>Приоритет в финансировании, который теперь будут отдавать «успешным» видам спорта, может лишить почвы те дисциплины, в которых наша страна и так с трудом развивается.</p>
<p>Кажется, что в спорте теперь всё будет по эффекту Матфея, когда богатые богатеют, а бедные беднеют.</p>
<p>«Не было у нас медалей в плавании и гимнастике, но появился Баландин в плавании в 2016 и Курбанов в гимнастике в 2024. И что, на плавание и гимнастику прольётся золотой дождь? А вот с 2016-го в плавании медалей нет, и что, сокращаем финансирование? Внезапная медаль была в 2000 году в пятиборье, завоевал её совершенно неожиданно для всех Парыгин. Прошла четверть века, что мы сейчас имеем в пятиборье? Безусловно, должны быть какие-то критерии в финансировании видов спорта, но как бы с водой не выплеснуть и ребёнка. Реформа эта должна быть подвижной, она не может иметь чётких границ. Выступили боксёры неудачно на Олимпиаде, так же, как и легкоатлеты, пловцы и многие другие, и что, давайте их прикроем или сократим сильно финансы? Значит, в следующий раз они выступят ещё хуже!»</p>
<p>рассуждает Алексей Кондрат.</p>
<p>Инфраструктура большого спорта в мегаполисах развивается не всегда достаточно эффективно, но строятся спортивные базы, манежи, велотреки. Жаль, что это так редко хотя бы по касательной задевает регионы.</p>
<p>«Все эти новые сооружения, безусловно, нужны и важны, но необходимо думать и о малых городах и посёлках. Там положение с инфраструктурой в некоторых, а может, и в большинстве видов просто аховое. Если в футболе, куда идёт подавляющее число денег, нет сертифицированных полей, и команды премьер-лиги проводят домашние игры на чужих полях, о чём остаётся говорить? А в маленьких городах, районных центрах время остановилось в лучшем случае на 80-х, 90-х»,</p>
<p>замечает Алексей Кондрат.</p>
<p><b>Пара слов о футболе</b></p>
<p>Как мы уже говорили, футбол в Казахстане удовольствие очень дорогое, и в ответ на вопрос, какие суммы там задействованы, нам ответили:</p>
<p>«Деятельность Казахстанской федерации футбола (КФФ) не финансируется из государственного бюджета. Бюджет КФФ формируется из следующих источников (данные за 2024 год): 4,9 млрд тенге от UEFA (программы развития UEFA, в том числе инвестиционное финансирование, а также финансирование за ТВ-права на игры национальной сборной РК); 6,1 млрд тенге спонсорские поступления (коммерческие права сборных команд, коммерческие права внутренних соревнований по футболу и футзалу, инфраструктурный проект ALAN ); 0,9 млрд тенге от FIFA (текущие расходы и специальные проекты); 0,6 млрд тенге от ПФЛК на судейское обслуживание профессиональных соревнований; 0,5 млрд тенге — образовательные услуги Технического центра КФФ; и прочие поступления (реализация билетов, имиджевой продукции, аренда и так далее)».</p>
<p>Итоговой суммой становятся 13 млрд тенге. Вроде и недорого? Но.</p>
<p>«По данным Антикора, за шесть лет клуб получил из государственного фонда SportQory более 50 млрд тенге. Сам по себе факт, что один из клубов получает от государства в разы больше своих соперников, красноречиво говорит о "справедливой и честной игре". При этом очень многие клубы умудряются влезать в огромные долги, за которые расплачиваются налогоплательщики. Это и ФК "Астана", долги на миллиарды тенге, и ФК "Ордабасы", долг за прошлый год более 4,2 млрд, который был покрыт из бюджета города, и другие клубы. Происходит очевидный шантаж. Руководители клубов, заведомо зная, что у них нет таких денег, покупают дорогостоящих легионеров, проводят сборы с размещением и питанием в пятизвёздочных отелях, летают чартерами. А когда к концу сезона обнаруживается, что клуб должен несколько миллиардов, вот тогда бросаются к акиму, обращаются к общественности, ведь они не бросят родную команду. И, как правило, это проходит. Их слегка журят, но долги покрывают. Как потом заявляет аким — мол, он закрыл их долги. Если бы он закрыл долги из своего кармана, то и бог с ним, но закрываются они за наш с вами счёт. И всё с начала»,</p>
<p>негодует Алексей Кондрат.</p>
<p><b>Разделяй и властвуй</b></p>
<p>Централизация власти, когда местные федерации должны будут подчиняться республиканской, по словам Алексея Кондрата, не придёт с новым законопроектом, именно так система работает и работала всегда.</p>
<p>«Спорить, ругаться, возможно, есть такое, но у республиканской федерации есть несколько прав, без которых областная или городская и пальцем не пошевелят. Например, по закону о спорте только республиканская федерация имеет право представлять Казахстан на международных соревнованиях. Или, что ещё важнее, только республиканская федерация имеет право организовывать и проводить республиканские чемпионаты, утверждать списки членов сборной, присваивать звания и фиксировать республиканские рекорды»,</p>
<p>рассуждает эксперт.</p>
<p>Мы спросили у Алексея Кондрата:как скажется пункт законопроекта, который запрещает легионеров? Ведь во многих видах спорта рейтинги так или иначе держатся иностранными спортсменами, выступающими за Казахстан. Часто они не в тройке лидеров, но забираются выше, чем отечественные спортсмены.</p>
<p>В ответ Алексей Кондрат предложил разграничить понятия легионера и натурализованного спортсмена. По его словам, в нашей стране чаще всего прибегают к услугам иностранных спортсменов, которые получают казахстанские паспорта, гражданство и становятся как бы своими. Кажется, эта система продолжит жить.</p>
<p>По словам эксперта, у Казахстана существует вероятность опуститься в рейтингах в теннисе, если там начнут пересматривать политику в отношении натурализованных спортсменов, а в остальных видах небывалых падений, как и небывалых взлётов, не предвидится.</p>
<p>Тренер считает: несмотря на то, что у спортсмена появляется паспорт Казахстана, к новой родине он относится чисто номинально, успехов, роста отечественных спортсменов, которые тренируются с лидером, получившим основной опыт где-то за пределами Казахстана, ждать не приходится.</p>
<p>«Что касается результатов в игровых видах, где играют легионеры. Да, очевидно, если убрать легионеров, уровень игр снизится, но и смотреть, когда из 22 игроков на игру выходят 20 иностранцев, согласитесь, как-то тоже не очень интересно. Вряд ли назовёшь это игрой казахстанских команд. А тем более когда это происходит за наш с вами счёт»,</p>
<p>считает заслуженный тренер РК.</p>
<p>Подводя итоги, Алексей Кондрат подчеркнул, что нужно думать и анализировать ситуацию шире, чем по количеству лицензий, медалей, легионеров, спортивных сооружений. Если бы атлеты и натурализованные, и отечественные привезли с Олимпиады-2024 шесть золотых медалей, а не одну, зашла бы тогда вообще речь о том, что отечественный спорт переживает кризис? По предположению Алексея, из-за шести золотых случился бы фокус: хвалили бы и добродушно журили. Просто сказались накопленные беды: слабый менеджмент, коррупция и одна золотая медаль.</p>
<p>Читайте также: Казахстан завершил выступление на Олимпийских играх в Париже</p>
<p><b>Нужна ли нам олимпийская медаль?</b></p>
<p>«К сожалению, как и на всём бывшем постсоветском пространстве, возможно, за исключением балтийских государств, у нас продолжает бытовать мнение, что развитие спорта и спортивные достижения являются показателем развития государства. Но сама жизнь опровергает это. За годы независимости спортсмены Казахстана завоевали 15 золотых медалей, а спортсмены Ямайки — 28. И наоборот, спортсмены Сингапура — всего одну. А на Олимпийских играх 2024 года в Париже Финляндия не завоевала ни одной медали вообще, даже бронзовой. И что? Вряд ли кто скажет, что уровень развития спорта в Финляндии ниже, чем в Казахстане. А в плане развития физической культуры и массового спорта, который направлен на развитие населения и в первую очередь детей, нам что до Финляндии, что до Сингапура, как до Луны»,</p>
<p>считает Алексей Кондрат.</p>
<p>Ну и о массовости. Упор планируют делать только на спорте высших достижений, и это вряд ли положительно скажется на том, чтобы дети захотели заниматься физкультурой, а как мы уже говорили, есть прямая корреляция между количеством спортсменов и успешностью результата.</p>
<p>Эксперт выступил в защиту программы АртСпорт или «Даму Бала»: по его мнению, в законопроекте предусмотрена возможность упразднить программу. Уже долгое время АртСпорт атакуют, обвиняя в «мёртвых душах», отсутствии условий, однако это далеко не всегда так. Журналисты Orda.kz тоже писали и о соглашении «Даму Бала», и о проверке общественниками поставщиков услуг, которая, по информации депутата, показала, что условия, в которых иной раз занимаются дети, ужасающие.</p>
<p>«Чиновникам из министерства не даёт покоя программа АртСпорт, которая и была задумана для того, чтобы привлечь к занятиям физической культурой и спортом как можно больше детей. И ведь поначалу получалось. Вместе с творческими кружками количество детей, занимающихся по этой программе, за первый год существования превысило 540 тысяч. Более 20 тысяч тренеров и педагогов получили работу»,</p>
<p>говорит Алексей.</p>
<p>По словам эксперта, на второй год ситуация стала меняться, власти захотели получить больше контроля над программой, и понеслось:</p>
<p>«Обвинения сыпались в воровстве, "мёртвых душах", приписках. Всех переплюнул аким Алматы, заявивший, что в программе работают сутенёры, содержатели борделей и проститутки. На запросы в акимат, прокуратуру с просьбой хотя бы указать, в каких кружках и секциях были обнаружены эти факты и какие приняты меры, конкретных ответов не было. Эта байка гуляет по страницам СМИ до сих пор при упоминании программы АртСпорт. Аким же заявил о хищении 1,2 млрд тенге, но все суды с предъявлением обвинений поставщикам акимат проиграл»,</p>
<p>заявил Алексей Кондрат.</p>
<p>Редакция продолжит следить следить за тем, какие пояснения будут сопровождать законопроект, и анализировать, насколько эффективны окажутся реформы. О программе АртСпорт и реформах, которые её ждут, мы, вероятно, напишем ещё не раз.</p>
</>);