import Card from "../../componets/card";
import "../index.css";
import "./index.css";

const News = () => {
  const newsPath = require
    .context("../../content/news/", false, /\.(js)$/)
    .keys();

  return (
    <div className="news-grid">
      {newsPath.map((rawKey) => {
        const key = rawKey.replace("./", "").replace(".js", "");

        const title = require("../../content/news/" + key + ".js").title;

        let img;
        try {
          img = require("../../content/images/" + key + ".jpg");
        } catch (error) {
          try {
            img = require("../../content/images/" + key + ".webp");
          } catch (error) {

          }
        }

        return (
          <Card
            key={key}
            to={"/news/" + key}
            src={img}
            title={title}
          />
        );
      })}
    </div>
  );
};

export default News;
