import "./index.css";

import { useParams } from "react-router-dom";

const OneNews = () => {
  const { newsId } = useParams();
  const { title, Body } = require("../../content/news/" + newsId + ".js");

  let img;
  try {
    img = require("../../content/images/" + newsId + ".jpg");
  } catch (error) {
    try {
      img = require("../../content/images/" + newsId + ".webp");
    } catch (error) {}
  }

  return (
    <div className="wrapper">
      <h1 className="news-title">{title}</h1>
      <div className="author-wrapper">
        <span className="author">Автор: Александра Мохирева</span>
      </div>
      {img && <img src={img} alt="Картинка" className="news-image-on-page" />}
      <div className="news-body">
        <Body />
      </div>
    </div>
  );
};

export default OneNews;
